import React from "react";
import styled from "styled-components";

import { ULink, FlatButton } from "./UniversalStyle";
import Header from "./Header";
import { PageContainer, Page } from "./UniversalStyle";
import { SquareGrid, SquareButton } from "./UniversalStyle";

import BackgroundImage from "../images/background.png";
import SteveHainesImage from "../images/SteveHaines.png";
import PersonIcon from "../images/icon1.png";

const InfoBlock = styled.div`
  display: flex;
  flex-direction: column;

  width: 80%;
  max-width: 600px;
  font-family: 'Source Sans Pro';
  color: rgb(191, 191, 191);

  h1 {
    color: white;
    margin: 0;
  }
`;

const InfoBlockContainer = styled.div `
  display: flex;
  flex-direction: row;
  align-items: center;
`;

function Home() 
{
  return (
    <div>
      <Header background={BackgroundImage} title={null}>
        <InfoBlockContainer>
          <InfoBlock>
            <h1>  <br/></h1>
            <p>
                 -     . 
                    ,      . 
                    ,      . 
                       . 
               -    . 
                 ,      .              
            </p>
            <ULink to="/"><FlatButton>  </FlatButton></ULink>
          </InfoBlock>
          <img alt="" src={SteveHainesImage}></img>
        </InfoBlockContainer>
      </Header>
      <PageContainer>
        <Page>
          <SquareGrid>
            <SquareButton to="/">
                <img alt="" src={PersonIcon}></img>
                <h3>Hello</h3>
                <p>Some Text</p>
            </SquareButton>
          </SquareGrid>
        </Page>
      </PageContainer>
    </div>
  );
}

export default Home;